<template>
  <div class="loginbox">
     <div class="logincontent">
        <div class="loginleft">
           <div class="loginlogoimg"></div>
           <img class="logincontentimg" src="../../assets/img/loginbg.jpg"></img>

        </div>
        <div class="loginright">
           <div class="logintitle">{{time}}好,</div>
           <div class="loginsubtitle">欢迎登录珠海多能协同运行管理平台！</div>
           <div class="logininputbox"><input type="text" placeholder="请输入账号" v-model="name"/></div>
           <div class="logininputbox"><input type="password" placeholder="请输入密码" v-model="mm"/></div>
           <div class="loginbtn" @click="loginsubmit">登录</div>

        </div>
     </div>
     <div class="loginbottom">Copyright  2018   北京国科恒通科技股份有限公司   All Rights Reserved.</div>
  </div>
</template>
<script>
import Vue from 'vue';
export default {
    data() {
        return {
          name:'',
          mm:'',
          time:'',





        };
    },
    components: {
    },
    methods: {
        loginsubmit(){
            const self=this;

            var name=Vue.trim(self.name);
            var mm=Vue.trim(self.mm);

            if(name=='admin'&&mm=='gedi2018'){
                var str=JSON.stringify({
                    'a':name,//用户姓名
                });
                console.log('str:'+str);
                Vue.setCookie('userinfo',str,1);
               location.href="/";
            }else{
                if(!name||!mm){
                    self.$alert('请输入用户名和密码','提示',{
                      confirmButtonText:'确定'
                    })

                }else{
                    self.$alert('用户名或密码错误','提示',{
                      confirmButtonText:'确定'
                    })

                }


            }


        }




    },
    mounted(){

          var d = new Date();
          var hour=d.getHours();
          if(hour>12){
              this.time='下午';
          }else{
              this.time='上午';
          }




    }
};


</script>
<style scoped>
    /* .loginbox{width:100%;height:100%;position:relative;background:url('../../assets/img/bg.jpg');background-size:100%;}  */
    .loginbox{
        width:100%;height:100%;position:relative;
        background:#003697;
        background: -webkit-linear-gradient(#00AAFF,#003697); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(#00AAFF,#003697); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(#00AAFF,#003697); /* Firefox 3.6 - 15 */
        background: linear-gradient(#00AAFF,#003697); /* 标准的语法 */
    }
    .logincontent{
        width:800px;height:500px;background:#fff;border:1px solid #fff;border-radius:4px;
        padding:80px 30px 20px 25px;box-sizing:border-box;
        margin:auto;
        position:absolute;
        left:50%;top:50%;
        margin-left:-400px;margin-top:-250px;
    }
    .loginleft{width:430px;height:100%;float:left;}
    .loginright{width:313px;height:100%;float:left;padding-left:20px;box-sizing:border-box;}
    .loginlogoimg{width:180px;height:33px;margin-left:100px;background:url('../../assets/img/nwlogo.png');}
    .logincontentimg{width:420px;height:270px;margin-top:20px;}
    .logintitle{font-size:24px;color:#222;line-height:40px;height:40px;font-weight:bold;}
    .loginsubtitle{font-size:16px;color:#222;height:30px;line-height:30px;font-weight:bold;}
    .logininputbox{margin-top:35px;}
    .logininputbox+.logininputbox{margin-top:16px}
    .logininputbox input{width:100%;padding:0 15px;box-sizing:border-box;height:35px;line-height:35px;font-size:16px;border:1px solid #F1F1F5;border-radius:30px;background:#F1F1F5;color:#878787;outline:0;-webkit-appearance: none;}
    .logininputbox input:focus{border:1px solid #66AACC;border-radius:30px;}
    .loginbtn{width:100%;height:35px;line-height:35px;color:#fff;background:#234EEC;border:1px solid #234EEC;border-radius:30px;font-size:16px;text-align:center;margin-top:35px;cursor:pointer;}
    .loginbottom{position:absolute;bottom:10px;left:0;width:100%;height:20px;line-height:20px;text-align:center;color:#fff;}
</style>



